<template>
  <div id="app">
    <div class="reg-box">

      <el-row>
        <el-col :span="24">
          <el-input type="tel" :maxlength="11" onkeypress="return event.keyCode>=48&&event.keyCode<=57" v-model.number="phoneNumber" placeholder="请输入手机号"></el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="15">
          <el-input type="text" v-model="pinNumber" placeholder="请输入验证码"></el-input>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-button v-if="getPinCodeState" type="primary" @click="phoneRegFun" class="get-pin-code">获取验证码</el-button>
          <el-button type="primary" plain v-if="!getPinCodeState" disabled>{{timeOut}}s</el-button>
        </el-col>

      </el-row>
      <el-row>
        <el-col :span="24">
          <el-button  @click="handleSubmitFun" class="submit-reg-button" :class="{'active':phoneNumberOk&&pinNumber}">立即注册</el-button>
        </el-col>
      </el-row>
     
    </div>

  </div>
</template>

<script>
  export default {
    name: 'app',
    data() {
      return {
        phoneNumber: '', //手机号
        pinNumber: '', //验证码
        phoneNumberOk: false, //手机号状态
        getPinCodeState: true, //获取验证码状态
        timeOut: 60 //获取验证码间隔
      }
    },
    methods: {
      
      
      phoneRegFun() {
        //验证手机号 
          let reg=/^1[34578]\d{9}$/;
          if(this.phoneNumber){
              if (reg.test(this.phoneNumber)) {
                  //正确 
                  this.phoneNumberOk= true;
                  this.getPinCodeState = !this.getPinCodeState;
                  this.timeOutFun();
                  //手机号 获取验证码 

              }else{
                //不是正确的手机号
                 this.phoneNumberOk= false;
                    this.$message({
              message: '手机号不正确',
              type: 'error',
              duration: 1000
            });
              }

          }else{
            //为空
             this.phoneNumberOk= false;
              this.$message({
              message: '手机号为空',
              type: 'error',
              duration: 1000
            });
          }
      },
      timeOutFun() {
        //定时器
        let vw = this;
        
        let timer = setInterval(function () {
          vw.timeOut--;
          if (vw.timeOut <= 0) {
            clearInterval(timer);
            timer = null;
            vw.timeOut =60 ;
            vw.getPinCodeState = !vw.getPinCodeState;
          }
        }, 1000);

      },
      handleSubmitFun(){

        //提交事件 
        //判断手机是否正确
        //验证码是否为空
        //提交 手机号 和 验证码
        
      }
      
    }
  }

</script>

<style>
  body {
    background: #f5f8fa;
    padding: 0;
    margin: 0;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: .4rem;
    width: 100%;
    background: #fff;
    padding-bottom: .3rem;
  }

  .reg-box {
    width: 80%;
    margin: 0 auto;
  }

  .reg-box input,
  .reg-box button {
    width: 100%;
    border-color: #999;
  }

  .get-pin-code {
    border: 0;
  }

  .get-pin-code,
  .submit-reg-button {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
  /*.get-pin-code:focus ,.submit-reg-button:focus{
    background: #fff;
    color: #5a5e66;
    border-color: #999;
  }*/
  .submit-reg-button {
    background: #fd9db4;
    color: #fff;
    border: 0;
  }
  .submit-reg-button:focus{
     background: #fd9db4;
     color: #fff;
  }
  .submit-reg-button:active{
    color: #fff;
  }
    .submit-reg-button.active {
    background: #fe416e;
  }
</style>



